import React from "react";
import { getPermissions } from "../util/auth.util";

const CheckPermission: any = ({
  slug,
  children,
}: {
  slug: string;
  children: React.ReactNode;
}) => {

  if (slug) {
    if (getPermissions()?.includes(slug)) {
      return <>{children}</>;
    }
  }

  return <></>;
};

export default CheckPermission;
