import { Modal, Button } from "antd";
import { useState } from "react";

const Modals = ({
  visible,
  confirmLoading,
  handleOk,
  handleCancel,
}: {
  visible: boolean;
  confirmLoading: boolean;
  handleOk: any;
  handleCancel: any;
}) => {
  const [modalText] = useState("Are you sure you want to delete this user ?");

  return (
    <>
      <Modal
        title="Delete User !"
        visible={visible}
        okText="Yes"
        cancelText="No"
        onOk={handleOk}
        confirmLoading={confirmLoading}
        onCancel={handleCancel}
      >
        <p>{modalText}</p>
      </Modal>
    </>
  );
};

export default Modals;
