import { DeleteOutlined, RollbackOutlined } from "@ant-design/icons";
import {
  Card,
  Col,
  Row,
  Form,
  Typography,
  Select,
  Button,
  Table,
  Space,
  Divider,
  Tabs,
  Tag,
  message,
} from "antd";

import { useEffect, useState } from "react";
import BreadcrumHeader from "../../components/breadcrumHeader";
import { getDropdownLists } from "../../store/actions/projectActionCreators";
import {
  AddProject,
  deleteProject,
  getSearchPerson,
  getUser,
  updateProject,
} from "../../store/actions/projectMembersActionCreators";
import moment from "moment";
import Modals from "../../components/modals";
import { useDebounce } from "use-debounce/lib";
import CheckPermission from "../../hoc/checkPermission";

export const ProjectMembers = (props: any) => {
  const [rolesList, setRolesList] = useState<any>([]);
  const [usersList, setUsersList] = useState<any>([]);
  const [userLoading, setUserLoading] = useState(false);
  const [addProjectloading, setAddProjectLoading] = useState(false);
  const [usersListDetails, setUsersListDetails] = useState<any>(null);
  const [page, setPage] = useState(1);
  const [search, setSearch] = useState("");
  const [visible, setVisible] = useState(false);
  const [options, setOptions] = useState([]);
  const [filter, setFilter] = useState<any>(null);
  const [filterDebounce] = useDebounce(filter, 500);
  const [inputSearch, setInputSearch] = useState("");
  const [projectRoleId, setProjectRoleId] = useState<any>(null);
  const [confirmLoading, setConfirmLoading] = useState(false);
  const [permissions, setPermissions] = useState<any>([]);
  const [form] = Form.useForm();

  const columns: any = [
    {
      title: "SN",
      dataIndex: "key",
      key: "key",
      responsive: ['xs', 'sm', 'md', 'xl']
    },
    {
      title: "Account",
      dataIndex: "personName",
      key: "personName",
      responsive: ['xs', 'sm', 'md', 'xl']
    },
    {
      title: "Access granted",
      key: "action2",
      render: (value: any) => {
        return (
          <div>
            {moment(value?.inviteDate, "YYYYMMDD").fromNow()}

            {value?.insertedPersonName && <span className="m-2">by</span>}
            {value?.insertedPersonName && <p> {value?.insertedPersonName}</p>}
          </div>
        );
      },
      responsive: ['xs', 'sm', 'md', 'xl']
    },

    {
      title: "Max role",
      key: "action1",
      render: (value: any) => {
        if (value?.projectRole === "Owner") {
          return <Tag>{value?.projectRole}</Tag>;
        } else {
          return (
            <Select
              className="w-50"
              placeholder="Select Role"
              defaultValue={value?.projectRoleListItemId}
              onChange={(role) => {
                changeRoles({
                  projectMemberId: value.projectMemberId,
                  projectSlug: value.projectSlug,
                  personId: [value.personId],
                  projectRoleListItemId: role,
                });
              }}
            >
              {rolesList.map((item: any, index: number) => {
                return (
                  <Select.Option key={index} value={item?.listItemId}>
                    {item?.listItemName}
                  </Select.Option>
                );
              })}
            </Select>
          );
        }
      },
      responsive: ['xs', 'sm', 'md', 'xl']
    },


    {
      title: "Action",
      key: "action",
      render: (value: any) =>
        value?.projectRole !== "Owner" ? (
          <CheckPermission slug="projectmember.delete">
            <Space size="middle">
              <a>
                <DeleteOutlined
                  onClick={() => {
                    setProjectRoleId(value?.projectMemberId);
                    setVisible(true);
                  }}
                />
              </a>
            </Space>
          </CheckPermission>
        ) : (
          <Space size="middle">
            <a>
              <RollbackOutlined />
            </a>
          </Space>
        ),
      responsive: ['xs', 'sm', 'md', 'xl']
    },
  ];

  const changeRoles = async (values: any) => {
    setUserLoading(true);
    const [response, error] = await updateProject(values);
    setUserLoading(false);
    if (response) {
      message.success(response.data);
      getUsers();
    }
    if (error) {
      message.error('Failed to change roles!');
    }
  };

  const handleSubmit = async (values: any) => {
    setAddProjectLoading(true);

    const projectData = {
      projectMemberId: 0,
      projectSlug: props.match.params.projectSlug,
      personId: values.email.map((item: any) => {
        return item.value;
      }),
      projectRoleListItemId: values.role,
    };

    const [response, error] = await AddProject(projectData);
    setAddProjectLoading(false);
    if (response) {
      form.resetFields();
      message.success(response.data);
      getUsers();
    }
    if (error) {
      message.error('Unable to add project members !');
    }
  };

  const { TabPane } = Tabs;

  async function fetchUserList(value: any) {
    setOptions([]);
    const [response, error] = await getSearchPerson(value);
    if (response) {
      setOptions(
        response.data.data.map((item: any) => {
          return {
            label: item?.name,
            value: item?.personId,
          };
        })
      );
    }

    if (error) {
    }
  }

  const getProjectRoles = async () => {
    const [response, error] = await getDropdownLists("ProjectRole");
    if (response) {
      setRolesList(
        response.data.filter((item: any) => item.listItemName !== "Owner")
      );
    }

    if (error) {
      message.error("Failed to display roles");
    }
  };

  const getUsers = async () => {
    setUsersList([]);
    setUserLoading(true);
    const [response, error] = await getUser({
      projectId: props.match.params.projectSlug,
      page,
      search,
    });
    setUserLoading(false);
    if (response) {
      setUsersList(
        response.data.data.map((item: any, index: any) => {
          return {
            ...item,
            key: index + 1,
          };
        })
      );
      setUsersListDetails(response.data);
    }

    if (error) {
      // message.error("Failed to display roles");
    }
  };

  useEffect(() => {
    getProjectRoles();
  }, []);

  useEffect(() => {
    fetchUserList(filterDebounce);
  }, [filterDebounce]);

  useEffect(() => {
    if (page || search) {
      getUsers();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [page, search]);

  return (
    <>
      <Modals
        visible={visible}
        confirmLoading={confirmLoading}
        handleOk={async () => {
          setConfirmLoading(true);
          const [response, error] = await deleteProject(projectRoleId);

          setConfirmLoading(false);
          if (response) {
            setVisible(false);
            message.success(response.data);
            getUsers();
          }

          if (error) {
            message.error('Unable to delete users !');
          }
        }}
        handleCancel={() => {
          setProjectRoleId(null);
          setVisible(false);
        }}
      />
      <Row className="mx-auto">
        <Col xs={23} lg={20}>
          <BreadcrumHeader projectName="TCM" routeName="Members" />
        </Col>
        <Col xs={23} lg={20}>
          <Typography.Title level={3}>Project Members</Typography.Title>
        </Col>
        <CheckPermission slug="projectmember.add">      <Col xs={23} lg={20}>
          <Card
            bordered
            style={{
              background: "none",
            }}
          >
            <Form onFinish={handleSubmit} form={form}>
              <Row gutter={24}>
                <Col xs={24} lg={24} className="gutter-row">
                  <label>Email Address</label>
                  <Form.Item
                    className="mt-2"
                    name="email"
                    rules={[{ required: true, message: "Email is Required" }]}
                  >
                    <Select
                      mode="multiple"
                      labelInValue
                      filterOption={false}
                      onSearch={(value) => {
                        setFilter(value);
                      }}
                      onBlur={() => {
                        fetchUserList("");
                      }}
                      options={options}
                    />
                  </Form.Item>
                </Col>

                <Col xs={24} lg={24} className="gutter-row">
                  <label>Choose a role permission</label>
                  <Form.Item
                    name="role"
                    className="mt-2"
                    rules={[{ required: true, message: "Role is Required" }]}
                  >
                    <Select placeholder="Select Role">
                      {rolesList.map((item: any, index: number) => {
                        return (
                          <Select.Option key={index} value={item?.listItemId}>
                            {item?.listItemName}
                          </Select.Option>
                        );
                      })}
                    </Select>
                  </Form.Item>
                </Col>

                <Col lg={18} className="mt-2">
                  <Button htmlType="submit" loading={addProjectloading}>
                    Invite
                  </Button>
                </Col>
              </Row>
            </Form>
          </Card>
        </Col>
        </CheckPermission>

        <CheckPermission slug="projectmember.read">
          <Col xs={23} lg={20} className="mt-4">
            <Tabs defaultActiveKey="1">
              <TabPane
                tab={
                  <span>
                    Members
                    <Tag className="m-2">
                      {usersListDetails?.totalRecords
                        ? usersListDetails?.totalRecords
                        : 0}
                    </Tag>
                  </span>
                }
                key="1"
              >
                <Divider />
                <Col lg={24} xs={24}>
                  <Table
                    columns={columns}
                    className="table table-responsive"

                    dataSource={usersList}
                    loading={userLoading}
                    pagination={{
                      defaultPageSize: 25,

                      onChange: (page) => {
                        setPage(page);
                      },
                    }}
                  />
                </Col>
              </TabPane>
            </Tabs>
          </Col>
        </CheckPermission>
      </Row>
    </>
  );
};

export default ProjectMembers;
