import axiosInstance from "../../axios/axios";
import { getProjectId, setHeaders } from "../../util/localStorage.util";
import { GET_PROJECT_DETAILS } from "./types/projectTypes";

export const getProjectList = async (data: any): Promise<[any, any]> => {
  setHeaders();
  try {
    const response = await axiosInstance({
      url: "/Project/GetProjectListFilterByModule",
      method: "POST",
      params: data,
    });
    return [response, null];
  } catch (err) {
    return [null, err];
  }
};

export const createProject = async (data: any): Promise<[any, any]> => {
  setHeaders();
  try {
    const response = await axiosInstance({
      url: "/Project/AddProject",
      method: "POST",
      data,
    });
    return [response, null];
  } catch (err) {
    return [null, err];
  }
};

export const getProjectDetails = async (): Promise<[any, any]> => {
  setHeaders();
  try {
    const response = await axiosInstance({
      url: `/Project/GetProjectByProjectId/${getProjectId()}`,
      method: "GET",
    });

    return [response, null];
  } catch (err) {
    return [null, err];
  }
};

export const getDropdownLists = async (category: any): Promise<[any, any]> => {
  setHeaders();
  try {
    const response = await axiosInstance({
      url: `/Common/GetListItemByListItemCategorySystemName/${category}`,
      method: "GET",
    });
    return [response, null];
  } catch (err) {
    return [null, err];
  }
};
