import { MenuOutlined } from "@ant-design/icons";
import { Button, Row, Col, Breadcrumb, Divider } from "antd";
import { useSelector, useDispatch } from "react-redux";
import { toogleDrawer } from "../store/actions/authActionCreators";

interface Props {
  projectName: string;
  routeName: string;
}

export const BreadcrumHeader = (props: Props) => {
  const dispatch = useDispatch();
  const { visible } = useSelector((state: any) => state.authReducer);
  return (
    <>
      <Row>
        <Col xs={8} lg={0}>
          <Button
            className="mobile__menu__toggle"
            onClick={() => {
              dispatch(toogleDrawer(visible));
            }}
          >
            <MenuOutlined />
          </Button>
        </Col>
        <Col xs={12} lg={12}>
          <Breadcrumb>
            <Breadcrumb.Item>{props?.projectName}</Breadcrumb.Item>
            <Breadcrumb.Item>
              <a href="#">TestCase management</a>
            </Breadcrumb.Item>
            <Breadcrumb.Item>
              <a href="#">{props?.routeName}</a>
            </Breadcrumb.Item>
          </Breadcrumb>
        </Col>
        <Divider />
      </Row>
    </>
  );
};

export default BreadcrumHeader;
